import { useEffect, useRef } from "react";
import "../styles/homegame.css";

export default function Home() {
  // Sử dụng useRef để tham chiếu đến navbar
  const navbarRef = useRef<HTMLDivElement>(null);
  const heroRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const navbar = navbarRef.current;

    if (navbar) {
      let lastScroll = 0;

      const handleScroll = () => {
        const currentScroll = window.pageYOffset;

        if (currentScroll <= 0) {
          navbar.style.transform = "translateY(0)";
          return;
        }

        if (currentScroll > lastScroll) {
          navbar.style.transform = "translateY(-100%)";
        } else {
          navbar.style.transform = "translateY(0)";
        }

        lastScroll = currentScroll;
      };

      window.addEventListener("scroll", handleScroll);

      return () => {
        window.removeEventListener("scroll", handleScroll);
      };
    }
  }, []);

  useEffect(() => {
    const heroSection = heroRef.current;
    const heroImages = heroSection?.querySelectorAll(
      ".hero-image"
    ) as NodeListOf<HTMLImageElement>;
    let currentImageIndex = 0;

    const changeHeroImage = (direction: number) => {
      if (heroImages) {
        heroImages[currentImageIndex].classList.remove("active");
        currentImageIndex =
          (currentImageIndex + direction + heroImages.length) %
          heroImages.length;
        heroImages[currentImageIndex].classList.add("active");
      }
    };

    const intervalId = setInterval(() => changeHeroImage(1), 5000);

    return () => clearInterval(intervalId);
  }, []);

  return (
    <>
      <nav ref={navbarRef} className="navbar">
        <div className="nav-container">
          <img
            src="/img/logo/FPT_Polytechnic.png"
            alt="Game Logo"
            className="nav-logo"
          />
          <div className="nav-links">
            <a href="#">GAME</a>
            <a href="#">GILF</a>
            <a
              href="https://facebook.com"
              target="_blank"
              rel="noopener noreferrer"
            >
              FACEBOOK
            </a>
            <a
              href="https://tiktok.com"
              target="_blank"
              rel="noopener noreferrer"
            >
              TIKTOK
            </a>
          </div>
        </div>
      </nav>

      <section ref={heroRef} className="hero">
        <div className="hero-background">
          <img
            src="/img/game/demo1.jpg"
            alt="Game 1"
            className="hero-image active"
          />
          <img src="/img/game/demo2.jpg" alt="Game 2" className="hero-image" />
          <img src="/img/game/demo3.jpg" alt="Game 3" className="hero-image" />
        </div>
        <button
          className="hero-nav left"
          onClick={() => {
            const heroSection = heroRef.current;
            const heroImages = heroSection?.querySelectorAll(
              ".hero-image"
            ) as NodeListOf<HTMLImageElement>;
            let currentImageIndex = 0;
            const changeHeroImage = (direction: number) => {
              heroImages[currentImageIndex].classList.remove("active");
              currentImageIndex =
                (currentImageIndex + direction + heroImages.length) %
                heroImages.length;
              heroImages[currentImageIndex].classList.add("active");
            };
            changeHeroImage(-1);
          }}
        >
          &#10094;
        </button>
        <button
          className="hero-nav right"
          onClick={() => {
            const heroSection = heroRef.current;
            const heroImages = heroSection?.querySelectorAll(
              ".hero-image"
            ) as NodeListOf<HTMLImageElement>;
            let currentImageIndex = 0;
            const changeHeroImage = (direction: number) => {
              heroImages[currentImageIndex].classList.remove("active");
              currentImageIndex =
                (currentImageIndex + direction + heroImages.length) %
                heroImages.length;
              heroImages[currentImageIndex].classList.add("active");
            };
            changeHeroImage(1);
          }}
        >
          &#10095;
        </button>
      </section>

      <section className="section">
        <div className="section-header">
          <h2 className="section-title">Games Boy</h2>
        </div>
        <div className="games-grid">
          {[1, 2, 3, 4].map((i) => (
            <div className="game-card" key={i}>
              <div className="game-image">
                <img
                  src="/img/game/500x500.png"
                  alt={`Game ${i}`}
                  className="game-image-inner"
                />
                <div className="game-overlay">
                  <h3 className="game-title">Game {i}</h3>
                </div>
              </div>
            </div>
          ))}
        </div>
      </section>

      <footer>
        <p>&copy; 2024 FPT Polytechnic Đồng Nai</p>
      </footer>
    </>
  );
}
